.home-page {
    height: 100%;
    width: 70%;
    margin-left: 15%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.game-selection {
    font-size: 24px;
    width: 60%;
}

.all-games {
    margin-top: 15px;
    width: 60%;
}

.game-item {
    display: flex;
    padding: 20px;
    border: 4px solid #3350c8;
    border-radius: 36px;
    align-items: center;
    justify-content: space-between;
}

.game-name {
    font-size: 22px;
}

.game-description {
    font-size: 14px;
    display: block;
}

.game-page {
    display: flex;
    height: 100%;
    width: 100%;
    align-items: center;
}

.question-box {
    flex: 3 1;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.question-form {
    margin-top: -10px;
    width: 95%;
}

.question-text {
    font-size: 32px;
    padding: 30px;
    text-align: center;
    border: 4px solid #3350c8;
    margin-bottom: 20px;
    border-radius: 36px;
    background-color: #031323;
}

.question-answers {
    text-align: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 15px;
}

.answer-button {
    flex: 0 1 47%;
    font-size: 20px;
    padding: 10px;
    border: 4px solid #3350c8;
    border-radius: 36px;
    background-color: #031323;
    margin-bottom: 12px;
    cursor: pointer;
    text-align: left;
}

.answer-button.--correct {
    animation: blink-success 1s steps(1) infinite;
    -webkit-animation: blink-success 1s steps(1) infinite;
}

.answer-button.--incorrect {
    animation: blink-error 1s steps(1) infinite;
    -webkit-animation: blink-error 1s steps(1) infinite;
}

.answer-button.--ignored {
    cursor: not-allowed;
    color: #031323;
}

.answer-button:hover:not(.--correct):not(.--incorrect):not(.--ignored) {
    background-color: #ebac00;
    color: black;
}

.answer-index {
    color: #ebac00;
    margin: 0 10px;
}

.answer-button:hover:not(.--correct):not(.--incorrect):not(.--ignored) .answer-index {
    color: black;
}

.answer-button.--correct .answer-index {
    color: white;
}

.answer-button.--incorrect .answer-index {
    color: white;
}

.answer-button.--ignored .answer-index {
    color: #031323;
}

.question-tree {
    flex: 1 1;
    border: 2px solid #3350c8;;
    border-radius: 30px;
    margin: 20px 20px 20px 0;
    padding: 15px 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    height: 95%;
}

.question-tree-entry {
    font-size: 30px;
    padding: 5px 25px;
    margin: 0 10px;
    border-radius: 18px;
    color: #ebac00;
}

.question-tree-entry.--active {
    color: white;
    background: #ebac00;
}

.question-tree-entry.--sure {
    color: white;
}

.question-tree-entry-index {
    text-align: right;
    display: inline-block;
    width: 30px;
    margin-right: 10px;
}

.cover-image {
    width: 45%;
}

.game-finished-page {
    height: 100%;
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-left: 15%;
}

.final-message {
    font-size: 32px;
    padding: 30px;
    text-align: center;
    border: 4px solid #3350c8;
    margin-bottom: 20px;
    border-radius: 36px;
    background-color: #031323;
    width: 100%;
}

.game-finished-buttons {
    display: flex;
    justify-content: space-around;
    width: 100%;
}

.give-up-button {
    width: 60%;
    margin-top: 20px;
    margin-left: 20%;
    text-align: center;
}

.help-buttons {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 100%;
    padding: 0 10px;
    margin-bottom: 10px;
}

.help-button {
    width: 100px;
    height: 100px;
    padding: 15px !important;
    border-radius: 50px !important;
    text-align: center;
}

.help-button.--used {
    opacity: 0.7;
    -webkit-filter: blur(2px);
            filter: blur(2px);
    cursor: not-allowed;
}

.help-button.--used:hover {
    background-color: #031323 !important;
    color: white !important;
}

.help-button:last-child {
    margin-right: 0;
}

.help-dialog {
    background-color: #031323 !important;
    color: white;
    padding: 20px 20px;
    border: 2px solid #3350c8 !important;
    border-radius: 30px !important;
    width: 60% !important;
}

.help-response {
    font-size: 24px;
    margin-top: 20px;
    margin-bottom: 40px;
    text-align: center;
}

.help-response-button {
    width: 50%;
    margin-left: 25%;
    text-align: center;
}

/* Blinking animations */

@keyframes blink-success {
    0%, 100% {
        background-color: #10f616;
    }
    50% {
        background-color: #031323;
    }
}

@-webkit-keyframes blink-success {
    0%, 100% {
        background-color: #10f616;
    }
    50% {
        background-color: #031323;
    }
}

@keyframes blink-error {
    0%, 100% {
        background-color: #db0000;
    }
    50% {
        background-color: #031323;
    }
}

@-webkit-keyframes blink-error {
    0%, 100% {
        background-color: #db0000;
    }
    50% {
        background-color: #031323;
    }
}

.not-found-page {
    height: 100%;
    width: 70%;
    display: flex;
    margin-left: 15%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.not-found-message {
    font-size: 32px;
    padding: 30px;
    text-align: center;
    border: 4px solid #3350c8;
    margin-bottom: 20px;
    border-radius: 36px;
    background-color: #031323;
    width: 100%;
}

/* http://meyerweb.com/eric/tools/css/reset/
   v2.0-modified | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
:focus {
    outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}

body {
    line-height: 1;
}

ol, ul {
    list-style: none;
}

blockquote, q {
    quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
    -webkit-appearance: none;
    -moz-appearance: none;
}

input[type=search] {
    -webkit-appearance: none;
    -moz-appearance: none;
    box-sizing: content-box;
}

textarea {
    overflow: auto;
    vertical-align: top;
    resize: vertical;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */

audio,
canvas,
video {
    display: inline-block;
    *display: inline;
    *zoom: 1;
    max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */

audio:not([controls]) {
    display: none;
    height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */

[hidden] {
    display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */

html {
    font-size: 100%; /* 1 */
    -webkit-text-size-adjust: 100%; /* 2 */
    -ms-text-size-adjust: 100%; /* 2 */
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */

a:focus {
    outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */

a:active,
a:hover {
    outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */

img {
    border: 0; /* 1 */
    -ms-interpolation-mode: bicubic; /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */

figure {
    margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */

form {
    margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */

fieldset {
    border: 1px solid #c0c0c0;
    margin: 0 2px;
    padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */

legend {
    border: 0; /* 1 */
    padding: 0;
    white-space: normal; /* 2 */
    *margin-left: -7px; /* 3 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */

button,
input,
select,
textarea {
    font-size: 100%; /* 1 */
    margin: 0; /* 2 */
    vertical-align: baseline; /* 3 */
    *vertical-align: middle; /* 3 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */

button,
input {
    line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */

button,
select {
    text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */

button,
html input[type="button"], /* 1 */
input[type="reset"],
input[type="submit"] {
    -webkit-appearance: button; /* 2 */
    cursor: pointer; /* 3 */
    *overflow: visible;  /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */

button[disabled],
html input[disabled] {
    cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */

input[type="checkbox"],
input[type="radio"] {
    box-sizing: border-box; /* 1 */
    padding: 0; /* 2 */
    *height: 13px; /* 3 */
    *width: 13px; /* 3 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */

input[type="search"] {
    -webkit-appearance: textfield; /* 1 */ /* 2 */
    box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */

button::-moz-focus-inner,
input::-moz-focus-inner {
    border: 0;
    padding: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */

textarea {
    overflow: auto; /* 1 */
    vertical-align: top; /* 2 */
}

/**
 * Remove most spacing between table cells.
 */

table {
    border-collapse: collapse;
    border-spacing: 0;
}

