body {
    background: rgb(2,0,36);
    background: -webkit-gradient(linear, left top, right top, from(rgba(2,0,36,1)), color-stop(50%, rgba(37,20,100,1)), to(rgba(3,19,45,1)));
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(37,20,100,1) 50%, rgba(3,19,45,1) 100%);
}

.main-wrapper {
    color: white;
    height: 100vh;
}

.default-button {
    font-size: 20px;
    padding: 10px 60px;
    border: 4px solid #3350c8;
    border-radius: 36px;
    background-color: #031323;
    cursor: pointer;
    color: white;
}

.default-button:hover {
    background-color: #ebac00;
